import { useNavigate } from "react-router-dom"
import { JsConst } from "../../constant/JsConst"
import { territoryConst } from "../../constant/Model/territoryConst"
import { constTranslate } from "../../constant/translate"
import { Agent } from "../../model/agent/Agent"
import { constContact } from "../../model/contact/constContact"
import { contactRequestService } from "../../services/contactRequest.service"
import { showErrorMsg, showSuccessMsg } from "../../services/eventBusService"
import { OptionsSelect } from "../OptionsSelect"
import { useState } from "react"
import { useSelector } from "react-redux"

export const AddContactModal = ({ currentUser, modalProps, isSelectDisabled, submitCb, btnText, title }) => {
    const { categories, jobTitles, companies, territories } = useSelector(state => state.categoryManager)
    const [form, setForm] = useState(modalProps)
    const [selectedCat, setSelectedCat] = useState(null)

    const navigate = useNavigate()

    const handleChange = ({ target }) => {
        let { name, value } = target
        setForm((prev) => ({ ...prev, [name]: value }));
    }

    const handleSelectChange = (name, value) => {
        value = handleCat(name, value)
        setForm((prev) => ({ ...prev, [name]: value }));
    }

    const handleCat = (name, value) => {
        if (name === JsConst.PHONE || name === JsConst.LINKEDIN_LINKS || name === JsConst.EMAIL) return value.split(",")
        else if (name === JsConst.CATEGORY) setSelectedCat(value)
        return value
    }

    const handleSubmit = async (ev) => {
        ev.preventDefault()
        submitCb(form)
    }

    const getCompanyByCategory = (companies) => {
        if (companies && selectedCat) {
            return companies.filter(company => company[JsConst.CATEGORY] === selectedCat.toLowerCase())
        } else return companies
    }

    return (
        <>
            <h1 className="addContactTitle">{title}</h1>
            <form className="addContactForm" onSubmit={handleSubmit}>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>Categories</label>
                    </div>
                    <OptionsSelect
                        selectedValue={modalProps[constContact.CATEGORY]}
                        options={categories}
                        cb={handleSelectChange}
                        accessor={JsConst.TITLE}
                        name={JsConst.CATEGORY}
                        isDisabled={isSelectDisabled} />
                </div>

                <div className="addContactItem">
                    <div className="label-container">
                        <label>Company</label>
                    </div>
                    <OptionsSelect
                        selectedValue={modalProps[constContact.COMPANY]}
                        options={getCompanyByCategory(companies)}
                        cb={handleSelectChange}
                        accessor={JsConst.COMPANY}
                        name={JsConst.COMPANY}
                        isDisabled={isSelectDisabled} />
                </div>

                <div className="addContactItem">
                    <label>Name</label>
                    <input name={JsConst.NAME} type="text" defaultValue={modalProps[constContact.NAME]} placeholder="John" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Family Name</label>
                    <input name={JsConst.FAMILY_NAME} type="text" defaultValue={modalProps[constContact.FAMILY_NAME]} placeholder="John" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>Job Titles</label>
                    </div>
                    <OptionsSelect
                        selectedValue={modalProps[constContact.JOBTITLE]}
                        options={jobTitles}
                        cb={handleSelectChange}
                        accessor={JsConst.TITLE}
                        name={JsConst.JOBTITLE}
                        isDisabled={isSelectDisabled} />
                </div>
                <div className="addContactItem">
                    <div className="label-container">
                        <label>Country</label>
                    </div>
                    <OptionsSelect
                        selectedValue={modalProps[constContact.COUNTRY]}
                        options={territories}
                        cb={handleSelectChange}
                        accessor={territoryConst.TERRITORY_NAME}
                        name={JsConst.COUNTRY}
                        isDisabled={isSelectDisabled} />
                </div>
                <div className="addContactItem">
                    <label>Description</label>
                    <input name={JsConst.DESC} type="text" defaultValue={modalProps[constContact.DESCRIPTION]} placeholder="description..." onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Email</label>
                    <input name={JsConst.EMAIL} type="text" defaultValue={modalProps[constContact.EMAIL].length ? modalProps[constContact.EMAIL][0] : ''} placeholder="john@gmail.com" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Phone</label>
                    <input name={JsConst.PHONE} type="text" defaultValue={modalProps[constContact.PHONE].length ? modalProps[constContact.PHONE][0] : ''} placeholder="+972.." onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>LinkedIn Links</label>
                    <input name={JsConst.LINKEDIN_LINKS} defaultValue={modalProps[constContact.LINKEDIN_LINKS].length ? modalProps[constContact.LINKEDIN_LINKS][0] : ''} type="text" onChange={handleChange} />
                </div>
                <div className="add-contact-button--container">
                    <button className="addContactButton">
                        {btnText}
                    </button>
                </div>
            </form>
        </>
    )
}